// NOTE: Querying Ethereum requires CORS enabled
// Use --rpccorsdomain "http[s]://hostname:port" or set up proxy

import URI from "urijs";
import AppConfiguration from "../../configuration.json";
import * as Ethers from "ethers";

const Ethereum = {
  URI: () => {
    return new URI()
      .protocol(AppConfiguration.ethereum.use_https ? "https" : "http")
      .host(AppConfiguration.ethereum.hostname)
      .port(AppConfiguration.ethereum.port)
      .path("")
      .hash("")
      .toString();
  },

  Provider: () => {
    return new Ethers.providers.JsonRpcProvider(Ethereum.URI());
  },

  async Balance(accountAddress) {
    return Ethers.utils.formatEther(await Ethereum.Provider().getBalance(accountAddress));
  },

  TransactionCount(accountAddress) {
    return Ethereum.Provider().getTransactionCount(accountAddress);
  },

  ListAccounts: () => {
    return [];
  },

  CreateAccount: ({password}) => {
    return [];
  },
};

export default Ethereum;
