import React from "react";
import Redirect from "react-router-dom/es/Redirect";
import PropTypes from "prop-types";
import requiredIf from "react-required-if";
import { BallPulse } from "../components/AnimatedIcons";

class Form extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      redirect: false
    };

    this.Redirect = this.Redirect.bind(this);
    this.HandleSubmit = this.HandleSubmit.bind(this);
    this.HandleCancel = this.HandleCancel.bind(this);
  }

  Redirect() {
    this.setState({
      redirect: true
    });
  }

  HandleSubmit(event) {
    event.preventDefault();
    if(!this.props.submitting) {
      this.props.OnSubmit(event);
    }
  }

  HandleCancel() {
    if(this.props.redirectOnCancel) {
      this.setState({ redirect: true });
    } else {
      this.props.OnCancel();
    }
  }

  Actions() {
    if(this.props.submitting) {
      return (
        <div className="actions-container loading">
          <div className="action-loading">
            <BallPulse />
          </div>
        </div>
      );
    }

    return (
      <div className="actions-container">
        <a className="action secondary" onClick={this.HandleCancel}>Cancel</a>
        <input className="action" type="submit" value="Create" />
      </div>
    );
  }

  render() {
    if((this.props.redirect || this.state.redirect) && this.props.redirectPath) {
      return (
        <Redirect push to={ this.props.redirectPath } />
      );
    }

    return (
      <div className="page-container">
        <form onSubmit={this.HandleSubmit}>
          <fieldset>
            <legend>{this.props.legend}</legend>
            { this.props.formContent }
            { this.Actions() }
          </fieldset>
        </form>
      </div>
    );
  }
}

Form.propTypes = {
  formContent: PropTypes.node.isRequired,
  OnSubmit: PropTypes.func.isRequired,
  OnCancel: requiredIf(PropTypes.func, props => !props.redirectOnCancel),
  legend: PropTypes.string,
  redirect: PropTypes.bool,
  redirectPath: PropTypes.string,
  redirectOnCancel: requiredIf(PropTypes.bool, props => props.OnCancel === undefined),
  submitting: PropTypes.bool
};

export default Form;
