import { ElvClient } from "elv-client-js";
import { FrameClient } from "elv-client-js/ElvFrameClient-min";
import ContentObject from "../models/ContentObject";

let Fabric = {
  Client() {
    return new FrameClient({
      target: window.parent,
      timeout: 10
    });
  },

  /* Libraries */

  ListContentLibraries: () => {
    return Fabric.Client().ContentLibraries();
  },

  GetContentLibrary: ({libraryId}) => {
    return Fabric.Client().ContentLibrary({libraryId});
  },

  CreateContentLibrary: async ({libraryName, libraryDescription, signer}) => {
    return await Fabric.Client().CreateContentLibrary({libraryName, libraryDescription, signer});
  },

  /* Objects */

  ListContentObjects: ({libraryId}) => {
    return Fabric.Client().ContentObjects({ libraryId });
  },

  GetContentObject: ({libraryId, objectId}) => {
    return Fabric.Client().ContentObject({ libraryId, contentHash: objectId });
  },

  GetContentObjectMetadata: ({libraryId, contentHash}) => {
    return Fabric.Client().ContentObjectMetadata({ libraryId, contentHash });
  },

  GetContentObjectVersions: ({libraryId, objectId}) => {
    return Fabric.Client().ContentObjectVersions({libraryId, objectId});
  },

  GetFullContentObject: async ({libraryId, objectId}) => {
    let contentObjectData = await Fabric.GetContentObject({ libraryId, objectId });
    const contentHash = contentObjectData.hash;
    contentObjectData.meta = await Fabric.GetContentObjectMetadata({ libraryId, contentHash });
    contentObjectData.parts = (await Fabric.ListParts({ libraryId, contentHash })).parts;

    let versions = (await Fabric.GetContentObjectVersions({ libraryId, objectId: contentObjectData.id })).versions;
    for(const version of versions) {
      version.meta = await Fabric.GetContentObjectMetadata({ libraryId, contentHash: version.hash });
      version.parts = (await Fabric.ListParts({ libraryId, contentHash: version.hash })).parts;
      version.verification = await Fabric.VerifyContentObject({libraryId, partHash: version.hash});

      for(const part of version.parts) {
        part.proofs = await Fabric.Proofs({libraryId, contentHash: objectId, partHash: part.hash});
      }
    }

    contentObjectData.versions = versions;

    return new ContentObject({ libraryId, contentObjectData });
  },

  /* Object creation / modification */

  CreateContentObject: ({
    libraryId,
    name,
    type,
    metadata = {}
  }) => {
    let requestParams = {
      type: "", // TODO: Why doesn't it accept a type?
      meta: {
        "eluv.name": name,
        "eluv.type": type,
        ...metadata
      }
    };

    return Fabric.Client().CreateContentObject({
      libraryId: libraryId,
      options: requestParams
    });
  },

  EditContentObject: ({
    libraryId,
    objectId
  }) => {
    return Fabric.Client().EditContentObject({
      libraryId,
      contentId: objectId,
      options: {}
    });
  },

  MergeMetadata: ({
    libraryId,
    writeToken,
    metadataSubtree,
    metadata
  }) => {
    Fabric.Client().MergeMetadata({
      libraryId,
      writeToken,
      metadataSubtree,
      metadata
    });
  },

  ReplaceMetadata: ({
    libraryId,
    writeToken,
    metadataSubtree,
    metadata
  }) => {
    Fabric.Client().ReplaceMetadata({
      libraryId,
      writeToken,
      metadataSubtree,
      metadata
    });
  },

  FinalizeContentObject: ({
    libraryId,
    writeToken
  }) => {
    return Fabric.Client().FinalizeContentObject({
      libraryId: libraryId,
      writeToken: writeToken
    });
  },

  // Convenience function to create and finalize object immediately
  // -- takes same arguments as CreateContentObject
  CreateAndFinalizeContentObject: async ({
    libraryId,
    name,
    type,
    metadata
  }) => {
    let createResponse = await Fabric.CreateContentObject({
      libraryId,
      name,
      type,
      metadata
    });

    return (
      await Fabric.FinalizeContentObject({
        libraryId,
        writeToken: createResponse.write_token
      })
    );
  },

  /* Files */

  CreateFileUploadJob: ({libraryId, writeToken, fileInfo}) => {
    return Fabric.Client().CreateFileUploadJob({libraryId, writeToken, fileInfo});
  },

  UploadFileData: ({libraryId, writeToken, jobId, fileData}) => {
    return Fabric.Client().UploadFileData({libraryId, writeToken, jobId, fileData});
  },

  GetUploadJobStatus: ({libraryId, writeToken, jobId}) => {
    return Fabric.Client().UploadJobStatus({libraryId, writeToken, jobId});
  },

  FinalizeUploadJobs: ({libraryId, writeToken}) => {
    return Fabric.Client().FinalizeUploadJobs({libraryId, writeToken});
  },

  DownloadFiles: ({libraryId, writeToken, filePath}) => {
    return Fabric.Client().DownloadFiles({libraryId, writeToken, filePath});
  },

  /* Parts */

  ListParts: ({libraryId, contentHash}) => {
    return Fabric.Client().ContentParts({libraryId, contentHash});
  },

  DownloadPart: ({libraryId, contentHash, partHash, format}) => {
    return Fabric.Client().DownloadPart({ libraryId, contentHash, partHash, format});
  },

  UploadPart: ({libraryId, writeToken, data}) => {
    return Fabric.Client().UploadPart({libraryId, writeToken, data});
  },

  PartUrl: ({libraryId, contentHash, partHash}) => {
    return Fabric.Client().PartUrl({libraryId, contentHash, partHash});
  },



  /* Naming */

  async GetByName({name}) {
    try {
      return await Fabric.Client().GetByName({name});
    } catch(error) {
      if(error.status === 404) {
        return undefined;
      }

      throw error;
    }
  },

  SetByName({name, target}) {
    return Fabric.Client().SetByName({name, target});
  },

  async GetObjectByName({name}) {
    try {
      let objectData = await Fabric.Client().GetObjectByName({name});

      return new ContentObject({contentObjectData: objectData});
    } catch(error) {
      if(error.status === 404) {
        return undefined;
      }

      throw error;
    }
  },

  SetObjectByName({name, libraryId, objectId}) {
    return Fabric.Client().SetObjectByName({name, libraryId, objectId});
  },

  DeleteName({name}) {
    return Fabric.Client().DeleteName({name});
  },

  VerifyContentObject: ({
    libraryId,
    partHash
  }) => {
    return Fabric.Client().VerifyContentObject({libraryId, partHash});
  },

  Proofs: ({libraryId, contentHash, partHash}) => {
    return Fabric.Client().Proofs({libraryId, contentHash, partHash});
  }
};

export default Fabric;
